<template>
  <div class="grid md:grid-cols-2 grid-cols-1 gap-4">
    <div class="grid grid-cols-2 gap-4">
      <div class="col-span-2">
        <Input v-model="data.user_id" type="text" label="User ID" :view="validation.user_id.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="error of validation.user_id.$errors">{{ error.$message }}. </span>
      </div>
      <div class="col-span-2">
        <Input v-model="data.merchant_account_code" type="text" label="Merchant Account Code" :view="validation.merchant_account_code.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="error of validation.merchant_account_code.$errors">{{ error.$message }}. </span>
      </div>
      <!-- <div class="col-span-2">
        <Input v-model="data.merchant_account_id" type="text" label="Merchant Account ID" :view="validation.merchant_account_id.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="error of validation.merchant_account_id.$errors">{{ error.$message }}. </span>
      </div> -->
      <div class="col-span-2">
        <Input v-model="data.mid" type="text" label="MID" :view="validation.mid.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="error of validation.mid.$errors">{{ error.$message }}. </span>
      </div>
      <div class="col-span-2">
        <Input v-model="data.location" type="text" label="Location" :view="validation.location.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="error of validation.location.$errors">{{ error.$message }}. </span>
      </div>
      <div class="col-span-2">
        <Input v-model="data.zip_code" type="text" label="Zip Code" :view="validation.zip_code.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="error of validation.zip_code.$errors">{{ error.$message }}. </span>
      </div>
      <div class="col-span-2">
        <Checkbox type="toggle" color="teal" value="1" value-off="0" v-model="data.is_default">
          Set as default
        </Checkbox>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import InputFile from 'components/InputFile.vue'
import { useVuelidate } from '@vuelidate/core'
import { required, email, validAddress, fileSize } from '@/validations/index'
import store from 'store'

export default defineComponent({
  name: 'Company',
  components: {
    Input,
    Checkbox,
    InputFile
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        user_id: null,
        merchant_account_id: null,
        merchant_account_code: null,
        mid: null,
        is_default: 0,
        location: null,
        zip_code: null
      })
    }
  },
  setup(props) {
    const data = reactive(props.data)

    const rules = {
      user_id: { required },
      // merchant_account_id: { required },
      merchant_account_code: { required },
      mid: { required },
      is_default: { required },
      location: { required },
      zip_code: { required }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    return {
      data,
      validation,
      validate,
      store
    }
  },
})
</script>
